import Vue from 'vue'
import Router from 'vue-router'
import BaseLearn from '@/components/BaseLearn.vue'
import UpLearn from '@/components/UpLearn.vue'


Vue.use(Router)


const routes = [
    {
        path: '/',
        component: BaseLearn,
        meta: ['基础学习']
    },
    {
        path: '/baseLearn',
        component: BaseLearn,
        meta: ['基础学习']
    },
    {
        path: '/upLearn',
        component: UpLearn,
        meta: ['提高学习']
    }
]
export default new Router({
    routes,
})
