<template>
  <div class="contentItem">
    <div class="tit">
      <img :src="content.icon" width="6%" />
      <span class="strong">{{content.tit.split('&')[0]}}</span>
      {{content.tit.split('&')[1]&&'&nbsp;-&nbsp;'+content.tit.split('&')[1]||''}}
    </div>
    <div class="wrapper">
      <div
        class="item"
        v-for="(itm,idx) in content.list"
        :key="idx"
        :class="{borderBottom:content.list.length!==1&&idx!==content.list.length-1}"
      >
        <div class="title">{{itm.tit}}</div>
        <div class="desc">{{itm.desc}}</div>
        <div class="videoBox" @click.stop="videoBoxHandleClick(idx)">
          <div class="bgBox" v-if="index!==idx">
            <img :src="itm.bg" width="100%" />
            <img class="playIcon" :src="playIcon" width="14%" />
          </div>
          <video
           :ref='idx'
            v-else
            width="100%"
            controls
            autoplay
            :poster='itm.bg'
            webkit-playsinline="true"
            playsinline="true"
            x-webkit-airplay="true"
            x5-video-player-type="h5"
            x5-video-player-fullscreen="true"
            x5-video-ignore-metadata="true"
            @click="event.stopPropagation()"
          >
            <source :src="itm.video" type="video/mp4" />
          </video>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ContentItem",
  components: {},
  props: {
    content: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      playIcon: require("@assets/images/paly.png"),
      videoShow: false,
      index:""//点击的index
    };
  },
  mounted() {},
  methods: {
    videoBoxHandleClick(idx) {
       this.index = idx
    }
  }
};
</script>


<style scoped lang='less'>
.contentItem {
  .tit {
    display: flex;
    align-items: center;
    margin: 0.36rem 0 0.26rem;
    .strong {
      font-weight: 600;
      margin-left: 0.1rem;
    }
  }
  .wrapper {
    background: #fff;
    border-radius: 0.1rem;
    padding: 0.2rem;
    .item {
      margin-top: 0.2rem;
      &.borderBottom {
        border-bottom: 1px solid #eaeaea;
      }
      &:first-child {
        margin-top: 0;
      }
      .title {
        font-weight: 700;
        line-height: 0.54rem;
        &:before {
          content: "|";
          display: inline-block;
          color: #ff8017;
          margin-right: 0.1rem;
        }
      }
      .desc {
        font-size: 0.26rem;
      }
      .videoBox {
        position: relative;
        width: 80%;
        margin: 0.24rem auto;
        .bgBox {
          width: 100%;
          .playIcon {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }
      }
    }
  }
}
</style>
